export default {
  name: 'App',
  data () {
    return {
      callouts: []
    }
  },
  created () {
    document.documentElement.style.setProperty(`--screenHeight`, window.innerHeight + 'px');

    const shuffled = possibilities.sort(() => Math.random() - 0.5);

    this.callouts = [
      shuffled[0],
      shuffled[1],
      shuffled[2],
    ].map(p => ({
      titles: p,
      change: Math.round((Math.random() - .1) * 50)
    }));

    setTimeout(() => location.reload(), 60000);
  }
